@import url(https://fonts.googleapis.com/css?family=Odibee+Sans|Open+Sans|Roboto|Sriracha|Trade+Winds|Akronim|Lacquer|Shadows+Into+Light|Dancing+Script|Amatic+SC|Bangers|Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
.container {
  width: 100vw;
  display: flex;
  flex-wrap: wrap;
}

.roboto {
  font-family: "Roboto";
}

.openSans {
  font-family: "Open Sans";
}

.odibeeSans {
  font-family: "Odibee Sans";
}
.sriracha {
  font-family: "Sriracha";
}
.tradeWinds {
  font-family: "Trade Winds";
}
.akronim {
  font-family: "Akronim";
}
.lacquer {
  font-family: "Lacquer";
}
.shadowsIntoLight {
  font-family: "Shadows Into Light";
}
.dancingScript {
  font-family: "Dancing Script";
}
.amaticSC {
  font-family: "Amatic SC";
}
.bangers {
  font-family: "Bangers";
}
.permanentMarker {
  font-family: "Permanent Marker";
}

.card-container {
  display: flex;
  flex-direction: column;
  min-width: 300px;
  height: 250px;
  margin: 20px;
  border-top: 1px solid darkgrey;
  flex-grow: 1;
}
.info-container {
  margin: 10px 0;
}

.font-name,
.font-author {
  font-family: "Open Sans";
  margin: 5px;
  font-size: 90%;
  font-weight: 700;
  color: darkgrey;
  line-height: 10px;
}

.font-name {
  color: black;
}
.button {
  background-image: none;
  color: crimson;
  font-weight: 500;
  font-size: 90%;
  border: none;
  background-image: none;
}

.show-text {
  width: 300px;
  flex-grow: 1;
  border: none;
  outline: none;
  font-size: 220%;
  line-height: 50px;
}

.wrapper {
  display: flex;
  justify-content: space-between;
}
.plus {
  height: 20px;
  width: 20px;
  margin-top: 10px;
  cursor: pointer
}

.box-container{
    display: flex;
    flex-direction: column;
    height: 200px;
    width: 300px;
    background-color: white;
    border: solid black;
    font-family: 'Open Sans';
}

.topBar{
    display: flex;
    flex-direction: row;
    background-color: #1F1F1F;
    height: 30px;
    justify-content: space-between;
    align-items: center;
}

#title{
    color: white;
    margin-left: 5px;
}

button{
    background-color: #1F1F1F;
    color: white;
    font-size: 20px;
    border: 0;
}

button:hover{
    background-color: rgb(68, 68, 68);
}

button:focus{
    border: none;
    outline: none;
}

.info-container2{
    display: flex;
    flex-direction: column;
}

.wrap{
    display: flex;
    justify-content: center;
}

.box{
    background-color: #EBEBEB;
    border-radius: 20px;
    width: 120px;
    padding: 5px;
    margin-top: 10px;
    font-size: 13px;
    padding-left: 10px;
}

.box2{
    background-color: #EBEBEB;
    padding: 10px;
    width: 270px;
    margin-top: 5px;
}

.line{
    height: 1px;
    width: 290px;
    background-color: #EBEBEB;
    margin-top: -5px;
}

p{
    font-size: 11px;
}
.navlist {
  height: 100px;
  width: 100%;
  background-color: white;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  list-style-type: none;
}
#nav {
  height: 40px;
  width: 100%;
  background-image: backgroundimage.png;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  list-style-type: none;
}
#input {
}

